@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

:root {
  /* color */
  --color-primary: #7f6d63;
  --color-secondary: #dac0a7;
  --color-point: #5b2d33;

  /* size */
  /* --base-space: 8px;
  --size-button: 60px;
  --size-border: 4px;
  --font-size: 18px; */
}

* {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
}

html {
  font-size: 16px;
  min-width: 1000px;
}

html,
body,
#root {
  height: 100%;
}

body {
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
     */
  /* color: #333; */
  color: #000;
  background-color: var(--color-secondary);
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* 
  background: linear-gradient(-45deg, #fda639, #b8f2ea, #e1eef6, #8c93f8);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite; */
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
