/* common container */
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.container * {
  box-sizing: border-box;
}

.container .inner {
  position: relative;
  width: 1000px;
  margin: auto;
  padding: 120px 70px 70px;
  z-index: 10;
}

/* main */
.main {
  position: relative;
  background: url(../src/assets/bg_main_02.jpg) 50% 50% no-repeat;
  background-size: cover;
}

.main:after {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(157, 157, 157, 0.2);
  width: 100%;
  height: 100%;
}

.main .title-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* width: 600px;
  height: 450px; */
  width: 550px;
  height: 350px;
  margin: auto;
  padding: 30px;
  background-color: rgba(218, 192, 167, 0.4);
  border-radius: 20px;
  /* border-radius: 50%; */
  text-align: center;
  color: #fff;
}

.main .title-box .title {
  margin: auto;
}

.main .title-box .label {
  /* padding: 5px 10px;
  border-radius: 5px;
  background-color: var(--color-primary);
  color: var(--color-primary);
  color: #fff; */
  display: block;
  font-weight: 500;
  font-size: 18px;
  text-align: left;
}

.main .title-box h1 {
  margin: 0 0 5px;
  font-size: 45px;
}

.main .title-box .date {
  display: block;
  font-size: 16px;
  font-weight: 500;
}

.main .title-box .made {
  display: block;
  text-align: right;
  /* align-self: flex-end; */
  font-weight: 400;
  /* margin-top: auto; */
}

/* sub */
.sub {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: var(--color-secondary); */
}

.sub .page-title {
  position: absolute;
  bottom: calc(100% - 14px);
  left: 0;
  font-family: "Poppins", sans-serif;
  color: #fff;
  line-height: 1;
  /* font-family: "Open-sans", sans-serif; */
}

.sub .page-title span {
  letter-spacing: -4px;
  margin-right: 10px;
  font-size: 80px;
  font-weight: 500;
}

.sub .page-title strong {
  display: inline-block;
  position: relative;
  font-size: 50px;
  font-weight: 700;
}

.sub .page-title strong::after {
  display: inline-block;
  content: "";
  width: 10px;
  height: 10px;
  margin-left: 5px;
  background-color: #fff;
}

.sub .content {
  position: relative;
  min-height: 500px;
  padding: 40px 50px;
  background-color: #fff;
  border-bottom: 30px solid #f1e3d5;
  /* box-shadow: 7px 7px 5px 0 rgba(248, 174, 76, 0.5); */
}

.sub .content:not(.no-bg)::before {
  display: block;
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(218, 192, 167, 0.3);
  right: 60px;
  bottom: 20px;
}

.sub .content:not(.no-bg)::after {
  display: block;
  content: "";
  position: absolute;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgba(218, 192, 167, 0.5);
  right: 25px;
  bottom: 30px;
}

.sub .content .title {
  margin-bottom: 20px;
  font-size: 36px;
}

.sub .inner-content.is-flex {
  display: flex;
}

.sub .inner-content.is-flex .img {
  margin: 0 20px 0 0;
}

.sub .inner-content .img {
  /* width: 135px; */
  margin: 30px 0;
  text-align: center;
}

.sub .inner-content .img img {
  /* width: 100%; */
}

.sub .inner-content .descr {
}

.sub .inner-content .descr-list li.no-flex {
  display: block;
}

.sub .inner-content .descr-list li {
  display: flex;
  /* align-items: center; */
  margin-bottom: 5px;
}

.sub .inner-content .descr-list li label {
  flex-shrink: 0;
  margin-right: 20px;
  color: var(--color-point);
  font-weight: 500;
}

.sub .inner-content .descr-list li label.date {
  min-width: 140px;
  margin-right: 15px;
  color: #1c1c1c;
}

.sub .inner-content .descr-list li p {
  margin-top: 1px;
  font-size: 15px;
  word-break: keep-all;
}

.sub .inner-content .descr-list.is-dot li {
  margin: 10px 0 0;
  padding-left: 10px;
}

.sub .inner-content .descr-list.is-dot li::before {
  display: block;
  content: "";
  width: 5px;
  height: 5px;
  margin: 10px 8px 0 0;
  flex-shrink: 0;
  background-color: var(--color-point);
}

.sub .btn-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}

.sub .btn-group button {
  border: 0;
  /* margin-left: 6px; */
  padding: 10px;
  text-transform: uppercase;
  background-color: var(--color-primary);
  color: #fff;
  transition: all 0.3s;
  cursor: pointer;
}

.sub .btn-group button.prev {
  margin-right: auto;
}

.sub .btn-group button.next {
  margin-left: auto;
}

.sub .btn-group button:hover {
  transform: translateY(5px);
  background-color: var(--color-point);
}

.sub .mark-txt {
  height: 100%;
  position: relative;
  margin-right: 10px;
  z-index: 10;
}

.sub .mark-txt:after {
  display: block;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 35%;
  background-color: var(--color-secondary);
  opacity: 0.3;
}

/* history list */
.sub .history-list {
  display: flex;
  flex-wrap: wrap;
}

.sub .history-list > li {
  padding: 10px;
  font-size: 13px;
  position: relative;
  z-index: 10;
  transition: 0.3s ease-in-out;
}

.sub .history-list > li > div:hover {
  transform: scale(1.05);
}

/* .sub .history-list > li:hover::after {
  width: 16px;
} */

.sub .history-list > li::after {
  display: block;
  content: "";
  width: 20px;
  height: 10px;
  background-color: var(--color-point);
  position: absolute;
  top: 50%;
  right: -10px;
  z-index: -1;
  transform: translateY(-50%);
}

.sub .history-list > li:nth-child(4n):after {
  bottom: 0;
  top: auto;
  left: 50%;
  transform: translate(-50%, 50%) rotate(90deg);
}

.sub .history-list > li:last-child:after {
  display: none;
}

.sub .history-list > li > div {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 170px;
  height: 170px;
  background-color: rgba(218, 192, 167, 0.4);
  /* border-radius: 50%; */
  border-radius: 20px;
  border: 5px solid var(--color-point);
  transition: 0.3s ease-in-out;
}

.sub .history-list > li:nth-child(even) > div {
  background-color: rgba(127, 109, 99, 0.7);
  /* color: #fff; */
  /* border: 5px solid var(--color-point); */
}

.sub .history-list > li .date {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  padding: 4px 6px;
  background-color: var(--color-point);
  color: #fff;
  border-radius: 5px;
  /* margin-top: 10px; */
  margin-bottom: 5px;
  font-size: 11px;
}

/* nav */
.nav {
  position: absolute;
  bottom: 100%;
  right: 0;
}

.nav a {
  display: inline-block;
  margin-left: 20px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  transition: all 0.3s;
  font-size: 14px;
}
.nav a:hover {
  color: var(--color-point);
  transform: translateY(-5px);
}

/* qna */
.qna {
  position: relative;
  background-color: #f5f9ff;
}

.qna .info {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 600px;
  padding: 40px;
  margin: auto;
  background: rgba(225, 238, 255, 0.5);
  border-radius: 20px;
}
